/* Full-page gradient background */
body {
  font-family: 'Poppins', sans-serif;
  background: linear-gradient(135deg, #ff9a9e 0%, #fad0c4 100%);
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  text-align: center;
  color: #fff;
}

/* Wrapper to align everything in the center */
.content-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 90vh;  /* Take full screen height */
  width: 100%;
}

/* Adjust Lottie animation position */
.animation {
  width: 300px;  /* Resize animation for better balance */
  max-width: 80%;
  height: auto;
  margin-bottom: 30px;  /* Space between animation and text */
  margin-left: 215px;  /* Move animation slightly to the right */
  animation: fadeIn 1.5s ease-in-out forwards;
}

/* Story text styling */
.slide-text {
  font-size: 1.6rem;  /* Slightly smaller font */
  font-weight: bold;
  max-width: 700px;
  padding: 20px;
  text-align: center;
  background: rgba(255, 255, 255, 0.3);
  border-radius: 15px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(10px);
  opacity: 0;
  animation: fadeIn 1.5s ease-in-out forwards 0.8s;
}

/* Align Next and Replay buttons with the Music button */
.next-btn,
.replay-btn {
  position: absolute;
  bottom: 50px;  /* Align with music button */
  left: 50%;
  transform: translateX(-50%);
  background: #ff4081;
  color: white;
  padding: 12px 35px;
  border: none;
  border-radius: 50px;
  font-size: 1.3rem;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 5px 15px rgba(255, 64, 129, 0.4);
}

.next-btn:hover,
.replay-btn:hover {
  background: #e91e63;
  transform: scale(1.05);
}


/* Adjust Music button positioning */
.music-btn {
  position: fixed;
  bottom: 50px;
  right: 50px;
  background: #ff4081;
  color: white;
  padding: 12px 25px;
  border-radius: 50px;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.3s ease;
}

.music-btn:hover {
  background: #e91e63;
}

/* Fade-out effect for the gift slide */
.fade-out {
  animation: fadeOut 1.5s ease-in-out forwards;
}

/* Keyframe for fading out */
@keyframes fadeOut {
  from {
    opacity: 1;
    transform: scale(1);
  }
  to {
    opacity: 0;
    transform: scale(0.9);
  }
}

/* Add hover effect for the clickable gift animation */
.animation:hover {
  transform: scale(1.1);
  transition: transform 0.3s ease-in-out;
}

/* Fade-in animation effect */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Responsive adjustments for smaller screens */
@media (max-width: 768px) {
  .animation {
    width: 200px;  /* Adjust size for smaller screens */
  }

  .slide-text {
    font-size: 1.3rem;
    padding: 15px;
  }

  .next-btn {
    padding: 10px 30px;
    font-size: 1.1rem;
  }

  .music-btn {
    bottom: 15px;
    right: 15px;
    padding: 10px 20px;
    font-size: 0.9rem;
  }
}

/* Larger surprise image */
.gift-image {
  width: 1000px; /* Increased size */
  height: 500px;
  max-width: 90%;
  border-radius: 15px;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.2);
  backdrop-filter: blur(10px);
  animation: fadeIn 1s ease-in-out forwards;
  margin-left: 50px; /* Adjust alignment */
}

/* Fade-in animation effect */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: scale(0.8);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

/* Adjust cursor behavior */
.animation:hover {
  transform: scale(1.1);
  transition: transform 0.3s ease-in-out;
}

/* Adjust button position */
.replay-btn {
  position: absolute;
  bottom: 50px;
  left: 50%;
  transform: translateX(-50%);
}